.navbar-custom {
  padding: 12px;
  width: 100%;
  border-radius: 0px;
  z-index: 999;
  margin-bottom: 0px;
  transition: all 0.5s;
}

.navbar-custom .logo-sticky .logo-dark {
  display: none;
}

.navbar-custom .logo-sticky .logo-light {
  display: inline-block;
}

.navbar-custom .navbar-nav li a {
  line-height: 26px;
  color: rgba(255, 255, 255, 0.5);
  font-size: 13px;
  transition: all 0.5s;
  background-color: transparent;
  padding: 6px 0;
  margin: 0 16px;
  font-weight: 600;
  text-transform: uppercase;
}

.navbar-custom .navbar-nav li a:after {
  content: "";
  display: block;
  width: 0;
  height: 2px;
  margin: 0 auto;
  background: #ffffff;
  transition: width 0.3s;
}

.navbar-toggler {
  font-size: 24px;
  margin-top: 5px;
  margin-bottom: 0px;
  color: #ffffff;
}

.navbar-custom .navbar-nav li.active a,
.navbar-custom .navbar-nav li a:hover,
.navbar-custom .navbar-nav li a:active {
  color: #ffffff;
}

.navbar-custom .navbar-nav li.active a:after,
.navbar-custom .navbar-nav li a:hover:after,
.navbar-custom .navbar-nav li a:active:after {
  width: 100%;
  transition: width 0.3s;
}

.menu-toggle {
  padding: 4.5px 10px !important;
}

.menu-toggle span {
  line-height: 27px;
}

.navbar-toggler:focus {
  outline: none;
}

.navbar-light {
  background-color: #ffffff;
  border-bottom: 1px solid #f2f2f2;
}

.navbar-light .nav-link:after {
  background: transparent;
}

.navbar-light .navbar-toggler {
  border-color: transparent;
}

.navbar-light .navbar-nav li a {
  color: rgba(52, 58, 64, 0.55);
}

.navbar-light .navbar-nav li.active a,
.navbar-light .navbar-nav li a:hover,
.navbar-light .navbar-nav li a:active {
  color: #5360fb;
}

.navbar-light .navbar-nav li.active a:after,
.navbar-light .navbar-nav li a:hover:after,
.navbar-light .navbar-nav li a:active:after {
  width: 100%;
  transition: width 0.3s;
  background-color: #5360fb;
}

.nav-sticky.navbar-custom {
  margin-top: 0px;
  background-color: #ffffff;
  box-shadow: 0 0 14px 0 rgba(0, 0, 0, 0.06);
  color: #000000 !important;
}

.nav-sticky.navbar-custom .logo-sticky .logo-dark {
  display: inline-block;
}

.nav-sticky.navbar-custom .logo-sticky .logo-light {
  display: none;
}

.nav-sticky .navbar-nav {
  margin-top: 0px;
}

.nav-sticky .navbar-nav li a {
  color: rgba(52, 58, 64, 0.55) !important;
}

.nav-sticky .navbar-nav li a:after {
  background: transparent;
}

.nav-sticky.navbar-custom .navbar-nav li.active a,
.nav-sticky.navbar-custom .navbar-nav li a:hover,
.nav-sticky.navbar-custom .navbar-nav li a:active {
  color: #5360fb !important;
}

.nav-sticky.navbar-custom .navbar-nav li.active a:after,
.nav-sticky.navbar-custom .navbar-nav li a:hover:after,
.nav-sticky.navbar-custom .navbar-nav li a:active:after {
  width: 100%;
  transition: width 0.3s;
  background: #5360fb !important;
}

@media (min-width: 200px) and (max-width: 768px) {
  .navbar-custom {
    margin-top: 0px;
    background-color: #ffffff;
  }
  .navbar-custom .navbar-nav {
    margin-top: 20px;
  }
  .navbar-custom .navbar-nav .nav-item .nav-link {
    display: inline-block;
    color: rgba(109, 98, 253, 0.75) !important;
    text-decoration: none;
  }
  .navbar-custom .navbar-nav .nav-item .nav-link:after {
    background: #5360fb;
  }
  .navbar-custom .logo-sticky .logo-dark {
    display: inline-block;
  }
  .navbar-custom .logo-sticky .logo-light {
    display: none;
  }
  .navbar-collapse {
    padding: 0 20px;
  }
  .navbar-toggler {
    color: #343a40;
  }
  .nav-sticky.navbar-custom .navbar-nav li.active a,
  .nav-sticky.navbar-custom .navbar-nav li a:hover,
  .nav-sticky.navbar-custom .navbar-nav li a:active {
    color: #5360fb !important;
  }
}

