  width: 100%;
}

.mfp-close:focus {
  outline: none;
}

.hero-2-bg {
  padding: 195px 0 140px 0px;
}

.hero-2-bg .hero-2-registration-form {
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.065);
