.hero-3-bg {
  padding: 250px 0 230px 0px;
}

.hero-3-bg .hero-3-title {
  font-size: 2.8rem;
}

.hero-3-bg .Subcribe-form form {
  position: relative;
  margin: 0px auto;
}

.hero-3-bg .Subcribe-form input {
  padding: 12px 20px;
  width: 60%;
  font-size: 17px;
  color: #4c5667 !important;
  border: none;
  outline: none !important;
  background-color: rgba(255, 255, 255, 0.85);
  border-radius: 30px;
  position: relative;
  top: 3px;
}

.hero-3-bg .Subcribe-form input::-webkit-input-placeholder {
  font-size: 15px;
  color: #868e96;
}

.hero-3-bg .Subcribe-form input:-ms-input-placeholder {
  font-size: 15px;
  color: #868e96;
}

.hero-3-bg .Subcribe-form input::-ms-input-placeholder {
  font-size: 15px;
  color: #868e96;
}

.hero-3-bg .Subcribe-form input::placeholder {
  font-size: 15px;
  color: #868e96;
}

.hero-4-bg {
  padding: 210px 0 200px 0px;
}

.hero-4-bg .hero-4-title {
  text-shadow: 5px 5px 8px rgba(0, 0, 0, 0.12);
  font-size: 2.9rem;
}

.hero-4-bg .hero-4-content {
  position: relative;
  z-index: 1;
}

.hero-4-bg .subcribe-form form {
  position: relative;
  max-width: 600px;
  margin: 0px auto;
}

.hero-4-bg .subcribe-form input {
  padding: 16px 20px;
  width: 100%;
  font-size: 17px;
  border: none;
  outline: none !important;
  padding-right: 160px;
  padding-left: 30px;
  background-color: #ffffff;
  border-radius: 30px;
}

.hero-4-bg .subcribe-form button {
  position: absolute;
  top: 5px;
  right: 5px;
}

.hero-4-bg .subcribe-form ::-webkit-input-placeholder {
  font-size: 16px;
  color: #868e96;
}

.hero-4-bg .subcribe-form :-ms-input-placeholder {
  font-size: 16px;
  color: #868e96;
}

.hero-4-bg .subcribe-form ::-ms-input-placeholder {
  font-size: 16px;
  color: #868e96;
}

.hero-4-bg .subcribe-form ::placeholder {
  font-size: 16px;
  color: #868e96;
}

.hero-4-bg .google-sign {
  background: #ffffff;
  display: inline-block;
  height: 48px;
  width: 48px;
  border-radius: 50%;
  line-height: 46px;
  text-align: center;
}

.hero-4-bg .google-sign img {
  max-width: 22px;
}

.hero-5-bg {
  padding: 180px 0 180px 0px;
}

.hero-5-bg .hero-5-title {
  font-size: 2.8rem;
}

.hero-5-bg .img-video {
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.4);
  border-radius: 0.25rem;
  transition: all 0.5s;
  padding: 10px;
}

.hero-5-bg .img-video .video-icon {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 50%;
  transform: translateY(50%);
}

.hero-5-bg .img-video .video-icon .play-icon {
  height: 64px;
  width: 64px;
  line-height: 64px;
  text-align: center;
  border-radius: 50%;
  display: inline-block;
  color: #ffffff;
  border: 1px solid #ffffff;
  box-shadow: inset 2px 2px 6px rgba(0, 0, 0, 0.3);
  position: absolute;
  top: 50%;
  left: 0px;
  right: 0px;
  transform: translateY(-50%);
  transition: all 0.5s;
}

.hero-5-bg .img-video .video-icon .play-icon:hover {
  background: #ffffff;
  color: #5360fb;
  box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.4);
}

.hero-6-bg {
  padding: 240px 0 250px 0px;
}

.hero-6-bg .hero-6-content {
  position: relative;
  z-index: 1;
}

.hero-6-bg .hero-6-content .hero-6-title-icon {
  display: inline-block;
  position: relative;
  -webkit-animation: bounce 0.7s infinite linear;
  animation: bounce 0.7s infinite linear;
}

.service-box {
  position: relative;
  padding: 65px 30px 30px;
  transition: all 0.5s;
  border-radius: 6px;
  box-shadow: 0 4px 18px rgba(52, 58, 64, 0.05);
}

.service-box .service-icon {
  position: relative;
  z-index: 1;
}

.service-box .service-icon:after {
  content: "";
  background: linear-gradient(45deg, transparent, rgba(109, 98, 253, 0.06));
  height: 70px;
  width: 70px;
  position: absolute;
  border-radius: 6px;
  transform: rotate(45deg);
  left: 0px;
  top: -16px;
}

.service-box:hover,
.service-box.active {
  background: linear-gradient(to right, #5360fb, #5a4efd);
}

.service-box:hover .service-icon,
.service-box.active .service-icon {
  color: #ffffff !important;
}

.service-box:hover .service-icon:after,
.service-box.active .service-icon:after {
  background: linear-gradient(45deg, transparent, rgba(255, 255, 255, 0.06));
}

.service-box:hover .service-title,
.service-box:hover a,
.service-box.active .service-title,
.service-box.active a {
  color: #ffffff !important;
}

.service-box:hover p,
.service-box.active p {
  color: rgba(255, 255, 255, 0.5) !important;
}

.features-img img {
  border-radius: 6px;
  box-shadow: 0 10px 40px -30px #343a40;
}

.right-icon {
  position: relative;
  top: 1px;
}

.counter-box {
  background: rgba(255, 255, 255, 0.025);
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
}

.testi-content {
  transition: all 0.5s;
  border-bottom: 2px solid transparent;
  padding: 30px;
  box-shadow: 0 3px 10px rgba(0, 0, 0, 0.05);
}

.testi-content .user-img {
  box-shadow: 0 5px 12px rgba(109, 98, 253, 0.2);
  transition: all 0.5s;
}

.testi-content .testi-icon {
  color: #b8b3fe;
}

.testi-content:hover {
  border-bottom: 2px solid #5360fb;
  box-shadow: 0 3px 16px rgba(0, 0, 0, 0.1);
}

.testi-content:hover .user-img {
  box-shadow: 0 7px 12px rgba(109, 98, 253, 0.3);
  transform: translateY(-6px);
}

.testi-content:hover .testi-icon {
  color: #5360fb;
}

.owl-theme .owl-controls .owl-page span {
  width: 14px;
  height: 4px;
  background-color: rgba(109, 98, 253, 0.4);
}

.owl-theme .owl-controls .owl-page.active span,
.owl-theme .owl-controls.clickable .owl-page:hover span {
  width: 20px;
  height: 4px;
  background-color: #5360fb;
}

.owl-carousel .owl-item img {
  width: inherit !important;
}

.owl-dot.active span {
  width: 20px !important;
  height: 4px !important;
  background-color: #5360fb !important;
}

.owl-dot span {
  width: 14px !important;
  height: 4px !important;
  background-color: rgba(109, 98, 253, 0.4) !important;
}

.clients-logo {
  opacity: 0.3;
  transition: all 0.5s;
}

.clients-logo:hover {
  opacity: 0.9;
  transition: all 0.5s;
}

.pricing-box {
  box-shadow: 0 3px 20px rgba(52, 58, 64, 0.03);
  border-radius: 6px;
  transition: all 0.5s;
}

.pricing-box .pricing-icon {
  box-shadow: 0 0 30px 0px rgba(109, 98, 253, 0.12);
  transition: all 0.5s;
}

.pricing-box .ribbon {
  position: absolute;
  left: 20px;
  top: 0px;
  background: linear-gradient(to right, #5360fb, #483afc);
  padding: 8px 4px 16px 4px;
  -webkit-clip-path: polygon(0 0, 100% 0, 100% 100%, 50% 80%, 0 100%);
  clip-path: polygon(0 0, 100% 0, 100% 100%, 50% 80%, 0 100%);
}

.pricing-box:hover,
.pricing-box.active {
  box-shadow: 0 4px 30px rgba(52, 58, 64, 0.06);
  transform: translateY(-10px);
  transition: all 0.5s;
}

.pricing-box:hover .pricing-icon,
.pricing-box.active .pricing-icon {
  background: linear-gradient(to right, #5360fb, #483afc) !important;
  color: #ffffff !important;
}

.bg-cta-overlay {
  background-image: url(data:image/png;base64,);
  opacity: 1;
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
}

.footer-sub-menu li {
  transition: all 0.5s;
}

.footer-sub-menu li a {
  color: #78828a !important;
}

.footer-sub-menu li a:hover {
  color: #494f54 !important;
}

.hero-two-bg {
  overflow: hidden;
}

.hero-two-img img {
  position: absolute;
  right: 0;
  top: 250px;
}

.hero-two-content {
  max-width: 500px;
}

.hero-two-content .hero-2-title {
  font-size: 2.7rem;
}

.testi-content-2 {
  transition: all 0.5s;
}

.testi-content-2 .texti-2-box {
  position: relative;
  box-shadow: 0 4px 18px rgba(52, 58, 64, 0.05);
}

.testi-content-2 .texti-2-box .testi-2-icon {
  color: #5360fb;
}

.testi-content-2 .texti-2-box:after {
  content: "";
  background: #ffffff;
  position: absolute;
  height: 20px;
  width: 20px;
  transform: rotate(45deg);
  left: 0;
  right: 0;
  margin: 0 auto;
  bottom: -9px;
  box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.012);
}

.testi-content-2 .testi-img-content {
  margin-top: 30px;
}

.testi-content-2 .testi-img-content img {
  max-width: 60px;
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.2);
}

.testi-content-2:hover {
  transform: translateY(-8px);
}

.testi-content-2:hover .texti-2-box {
  background: #5360fb;
  box-shadow: 0 4px 20px rgba(109, 98, 253, 0.3);
}

.testi-content-2:hover .texti-2-box p {
  color: rgba(255, 255, 255, 0.6) !important;
}

.testi-content-2:hover .texti-2-box .testi-2-icon {
  color: #ffffff;
}

.testi-content-2:hover .texti-2-box:after {
  background: #5360fb;
}

.testi-content-2:hover .testi-img-content img {
  box-shadow: 0 4px 12px rgba(109, 98, 253, 0.4);
}

.post-content {
  box-shadow: 0 4px 18px rgba(52, 58, 64, 0.05);
}

@-webkit-keyframes bounce {
  0% {
    top: 0;
  }
  50% {
    top: -0.2em;
  }
  70% {
    top: -0.2em;
  }
  100% {
    top: 0;
  }
}

@keyframes bounce {
  0% {
    top: 0;
  }
  50% {
    top: -0.2em;
  }
  70% {
    top: -0.2em;
  }
  100% {
    top: 0;
  }
}

@media (max-width: 425px) {
  .hero-title {
    font-size: 30px !important;
  }
  .hero-2-bg {
    padding: 140px 0px 260px !important;
  }
  .hero-2-title,
  .hero-4-title,
  .hero-5-title {
    font-size: 2rem !important;
  }
  .service-2-content h3 {
    font-size: 1.5rem !important;
  }
  .hero-4-bg,
  .hero-5-bg {
    padding: 130px 0 140px 0px;
  }
  .hero-6-bg {
    padding: 130px 0 110px 0px;
  }
}

@media (max-width: 768px) {
  .hero-1-bg {
    padding: 120px 0px 50px 0px;
    height: auto !important;
  }
  .hero-1-bg .video-play-icon {
    background: #5360fb !important;
    box-shadow: 0 0 5px 8px rgba(109, 98, 253, 0.2) !important;
    color: #ffffff !important;
  }
  .hero-two-bg {
    padding: 140px 0px 420px;
    height: auto !important;
  }
  .hero-two-bg .hero-two-img img {
    top: auto;
    left: 0;
    margin: 0 auto;
  }
  .cta-content {
    text-align: center !important;
  }
  .cta-content .cta-btn {
    text-align: center !important;
  }
  .footer-about-content {
    text-align: center;
  }
  .hero-3-bg {
    padding: 150px 0 110px 0px;
  }
  .hero-3-bg .hero-3-title {
    font-size: 1.8rem !important;
  }
  .hero-3-bg .Subcribe-form input {
    width: 90%;
    margin-bottom: 25px;
  }
  .hero-3-bg .Subcribe-form .btn {
    margin: 0 !important;
  }
}

@media (min-width: 769px) and (max-width: 1024px) {
  .hero-two-bg {
    padding: 180px 0px 520px !important;
    height: auto !important;
  }
  .hero-two-bg .hero-two-img img {
    top: auto;
    left: 0;
    margin: 0 auto;
  }
}
